import { request } from "./request"
//get 
export function getHomeInfo() {
    return request({
        url: '/api/getIndex.do'
    })

}
export function getList(data) {

    //console.log(data)
    return request({
        url: '/api/getProductList.do',
        data: data,
        method: 'post'
    })
}


//
export function getProductDetail(id) {
    return request({
        url: '/api/getProductDetail.do?Pro_Id=1' //

    })
}
//
export function getMyUserInfo() {
    return request({
        url: '/api/getUserInfo.do' //
    })
}
// 
export function getProductList() {
    return request({
        url: '/api/getProductList.do',
        data: { PageCount: 100, CurrentPage: 1, Key: '', P_Type_Menu_Id: 2 },
        method: 'post'
    })
}
//

export function login(data) {

    return request({
        url: '/api/login.do',
        data: { Account: 'admin', Password: '12234567' },
        method: 'post'
    })
}

