import Vue from 'vue'
import Vuex from 'vuex'
import { getMenu } from '@/network/shopMenu';
Vue.use(Vuex);
const state = {
    menulist: [], //
    navbar: ''
}
const getters = {
    //
    menulist: state => state.menulist,
    list: (state, getters) => { //gettergetter.cartProducts
        let data = getters.menulist;
        data.forEach(function(item) {
                delete item.children
            })
            //   id  KEY  map 
        var map = {}
        data.forEach(function(item) {
                map[item.id] = item
            })
            // console.log(map)
        var val = []
        data.forEach(function(item) {
            // pid,mapid
            var parent = map[item.pid]
                // ,
            if (parent) {
                // parent[0].show = false
                ;
                (parent.children || (parent.children = [])).push(item)
            } else {
                //mapID, item val
                val.push(item)
            }
        })
        return val;
    },
    navbar: state => state.navbar
}
const actions = {
    //
    getMenuList({ commit }) {
        getMenu()
            .then(res => {
                let data = res.data.data
                commit('getMenuList', data)
            })
            .catch(e => {
                console.log(e)
            })
    },
    /*  */
    setNav({ commit }, str) {
        commit('setnav', str)
    }
}
const mutations = {
    getMenuList(state, data) {
        // console.log(data)
        state.menulist = data;
        // state.navbar = 
        // return state;
    },
    setnav(state, data) {
        state.navbar = data;
        // console.log(data + "setnav store");
        // console.log(state.navbar)
        // console.log(state)
        // console.log(state.menulist)
    }
}
export default {
    state,
    mutations,
    getters,
    actions
}