<template>
    <div id="addcart"  v-show="isshow">
        <div class="addcart-body">
            <div class="addcart-header">
                <span>
                    <button type="button" title="" @click="handle">X</button>
                </span>
                 <h2>{{msg}}</h2>
            </div>
            <div class="addcart-content">
                <div>
                    <p class="p10">
                        <span>{{kind}}</span> 
                        <span>{{totalNum}}</span>   
                        <span>{{totalPrice}}</span>
                    </p>
                    <div class="btnBar">
                        <button class="cart_btn btn-default" @click="gohome"></button>
                        <button class="cart_btn btn-danger" @click="gocart"></button>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import { mapGetters } from 'vuex'
   export default {
        name: "addcart",
        data(){
            return {
               msg:"",

            }
        },
        props:['isshow','kind','totalNum','totalPrice'],
        methods:{
            handle(){
                this.$emit('showbox', this.isshow);
            },
            gohome(){
                this.$router.push("/home");
            },
            gocart() {
                this.$router.push("/cart");
            }

        },
   }
</script>
<style lang="scss" scoped>
@import "~assets/css/pop_up/addcart.scss";
</style>
